<template>
  <div class="registry">
    <van-cell-group inset>
      <!-- 输入任意文本 -->
      <van-field
        v-model="usrinf.email"
        label="用户名"
        placeholder="请输入电子邮箱"
        @blur="checkEle('email')"
        :required="err.email"
        :errorMessage="errInfo.email ? errMsg.email : ''"
      />
      <!-- 输入手机号，调起手机号键盘 -->
      <van-field
        v-model="usrinf.tel"
        type="tel"
        label="手机号"
        placeholder="请输入手机号"
        @blur="checkEle('tel')"
        :required="err.tel"
        :errorMessage="errInfo.tel ? errMsg.tel : ''"
      />
      <!-- 输入密码 -->
      <van-field
        v-model="usrinf.pwd1"
        @blur="checkEle('pwd1')"
        placeholder="请设置密码"
        type="password"
        label="设置密码"
        :required="err.pwd1"
        :errorMessage="errInfo.pwd1 ? errMsg.pwd1 : ''"
      />
      <van-field
        v-model="usrinf.pwd2"
        placeholder="请确认密码"
        type="password"
        label="确认密码"
        :required="err.pwd2"
        :errorMessage="errInfo.pwd2 ? errMsg.pwd2 : ''"
      />
    </van-cell-group>
    <div class="btn">
      <van-button block type="primary" @click="nextStep" :disabled="isNo"
        >下一步</van-button
      >
    </div>
  </div>
</template>
<script>
import { field, cellGroup, Button } from "vant";
export default {
  name: "registryPage",
  components: {
    [Button.name]: Button,
    [field.name]: field,
    [cellGroup.name]: cellGroup,
  },
  data: () => ({
    errorMsg: "",
    usrinf: {
      tel: "",
      email: "",
      pwd1: "",
      pwd2: "",
    },
    err: {
      email: false,
      tel: false,
      pwd1: false,
      pwd2: false,
    },
    errInfo: {
      email: false,
      tel: false,
      pwd1: false,
      pwd2: false,
    },
    errMsg: {
      email: "邮箱格式不正确",
      tel: "手机号码格式不正确",
      pwd1: "密码过于简单",
      pwd2: "两次输入密码不一致",
    },
  }),
  created() {},
  methods: {
    checkEle(el) {
      console.log(el, "ellll====");
      console.log(this.usrinf[el], "888888");
      this.err[el] = this.usrinf[el] === "";
      if (this.usrinf[el] === "") {
        this.errInfo[el] = false;
        return;
      }
      let reg = "";
      let res = "";
      switch (el) {
        case "email":
          reg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
          break;
        case "tel":
          reg =
            /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
          break;
        case "pwd1":
          reg = /[-\da-zA-Z`=\\;',./~!@#$%^&*()_+|{}:"<>?]{8}/;
          break;
        case "pwd2":
          res = true;
          break;

        default:
          break;
      }
      this.errInfo[el] = res
        ? this.usrinf.pwd1 !== this.usrinf.pwd2
        : !reg.test(this.usrinf[el]);
      console.log(this.errInfo[el], "8888");
    },
    nextStep() {
      console.warn(">>>>>>>>>>>>点击了下一步按钮<<<<<<<<<<<<<");
      console.log(this.usrinf);
      this.$dialog
        .alert({
          message: "注册成功，跳转去登录",
        })
        .then(() => {
          this.$router.push("/login");
        });
    },
  },
  computed: {
    errorShow() {
      const Reg =
        /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
      return Reg.test(this.usrinf.tel);
    },
    isNo() {
      const arr = Object.values(this.usrinf);
      const err = Object.values(this.errInfo);
      return arr.includes("") || err.includes(true);
    },
  },
  watch: {
    "usrinf.pwd2": {
      handler: function (val) {
        if (!val) return;
        this.errInfo.pwd2 = val !== this.usrinf.pwd1;
      },
    },
  },
};
</script>
<style lang="scss" scoped>
@import "index";
</style>
